import React from 'react';
import {Button, TextField} from '@material-ui/core';
import {useDispatch, useSelector} from 'react-redux';
import {updateEsdServerUrl, updateTgId} from './reducer';
import {getValueFromEvent} from "../utils/viewHelper";

const updateConfig = ({esdServerUrl, tgId}) => {
    sessionStorage.setItem("esdServerUrl", esdServerUrl);
    sessionStorage.setItem("tgId", tgId);
}

export default function Main() {
    const config = useSelector((state) => state.main.config);
    const dispatch = useDispatch();

    return (
        <div>
            <TextField label="ESD Server URL" value={config.esdServerUrl}
                       onChange={(event) =>
                           dispatch(updateEsdServerUrl({
                               esdServerUrl: getValueFromEvent(event)
                           }))}/><br/>

            <TextField label="TgID" value={config.tgId}
                       onChange={(event) =>
                           dispatch(updateTgId({
                               tgId: getValueFromEvent(event)
                           }))}/><br/>

            <Button id="update-config" variant="contained"
                    onClick={() => updateConfig(config)}>
                Update
            </Button>
        </div>
    )
}