import {createSlice} from '@reduxjs/toolkit';

export const Slice = createSlice(
    {
      name: 'alert',
      initialState: {
        begin: {
          startTime: new Date(),
          stopTime: new Date(),
          capacity: 0,
        },
        end: {
          stopTime: new Date(),
        },
      },
      reducers: {
        updateBeginStartTime: (state, action) => {
          const {startTime} = action.payload;
          const begin = state.begin;

          state.begin = {
            ...begin,
            startTime,
          };
        },
        updateBeginStopTime: (state, action) => {
          const {stopTime} = action.payload;
          const begin = state.begin;

          state.begin = {
            ...begin,
            stopTime,
          };
        },
        updateBeginCapacity: (state, action) => {
          const {capacity} = action.payload;
          const begin = state.begin;

          state.begin = {
            ...begin,
            capacity,
          };
        },
        updateEndStopTime: (state, action) => {
          const {stopTime} = action.payload;
          const end = state.end;

          state.end = {
            ...end,
            stopTime,
          };
        },
      },
    });

export const {
  updateBeginStartTime,
  updateBeginCapacity,
  updateBeginStopTime,
  updateEndStopTime,
} = Slice.actions;

export default Slice.reducer;